@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gloria+Hallelujah&display=swap);
* {
    box-sizing: border-box;
    font-family: 'Rubik', sans-serif;
}
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
.app {
  background: url(/grocery-list/static/media/bg.c6cb0607.jpg) center center no-repeat;
  background-size: cover;
}
.container {
  max-width: 1440px;
  margin: 0 auto;
}

.header{background:linear-gradient(90deg, rgba(31,29,30,0.8) 0%, rgba(127,144,0,0.8) 100%);box-shadow:0 0px 20px 5px #000;position:relative;display:flex;align-items:center;justify-content:center;font-size:32px;font-weight:600;text-transform:uppercase;letter-spacing:3px;color:#bac43a;text-shadow:0 0 5px #000;padding:10px 40px}.header-logo{width:40px;margin:0 40px}@media (max-width: 1536px){.header{justify-content:flex-start}}.header::after{content:'Serhii Lisniakov © 2020';font-size:9px;display:flex;justify-content:center;align-items:center;position:fixed;left:0;bottom:0;z-index:1;width:100%;height:16px;background:linear-gradient(270deg, #1f1d1e 0%, rgba(127,144,0,0.5) 100%)}

.list-filter{font-family:'Indie Flower', cursive;font-size:21px}.list-filter label{font:inherit;padding-right:20px;margin-left:10px;cursor:pointer;background:url(/grocery-list/static/media/non-check.60b86e29.svg) center right no-repeat;background-size:15px;transition:.4s}.list-filter input[type='checkbox']{width:0.01px}.list-filter input[type='checkbox']:checked+label{background:url(/grocery-list/static/media/check.45a61450.svg) center right no-repeat;background-size:15px}.list-sort{margin-top:20px;display:flex}.list-sort .keys{display:flex;flex-direction:column}.list-sort span{font-family:'Indie Flower', cursive;font-size:21px;letter-spacing:2px;width:120px;margin-left:10px;margin-bottom:3px;cursor:pointer;background:rgba(255,252,242,0.37);padding:1px 5px;border-radius:5px;transition:.3s}.list-sort span.active{transition:.3s;background:#3db490}.list-actions{width:400px;height:400px;margin-right:20px;background:url(/grocery-list/static/media/notes.3888b738.png) center center no-repeat;background-size:contain;padding-top:130px;padding-left:90px;line-height:1;font-weight:600}.list-actions-inner{transform:rotate(1deg)}

.desk{padding:20px 15px;height:calc(100vh - 60px);display:flex}.list{overflow-y:scroll;height:79vh;max-width:800px;flex-grow:1;margin-left:auto;padding:30px 30px 30px 100px;background:url(data:image/png;base64,) center left;background-size:cover;transform:rotate(-0.5deg);border-radius:10px}.list::-webkit-scrollbar{width:0}.list-empty{height:100%;display:flex;justify-content:center;align-items:center;font-style:italic}.item{display:grid;padding:5px 0;grid-template-columns:1fr 100px 100px 35px;border-bottom:1px solid rgba(73,52,50,0.589);align-items:center}.item-name .name{position:relative;display:inline-block;font-family:'Gloria Hallelujah', cursive;line-height:1.1;font-weight:600;letter-spacing:3px;font-size:26px;white-space:nowrap}.item-name .name::first-letter{text-transform:uppercase}.item-name .name .marker{position:absolute;top:-2px;left:-10px;width:0;height:120%;z-index:-1;-webkit-animation:marker .5s ease-in-out forwards;animation:marker .5s ease-in-out forwards;-webkit-animation-delay:.7s;animation-delay:.7s}.item-name .name .marker::after{position:absolute;content:'';width:0;left:-5px;height:100%;z-index:-1;-webkit-animation:markerShadow .5s ease-in-out forwards;animation:markerShadow .5s ease-in-out forwards;-webkit-animation-delay:1.5s;animation-delay:1.5s}.item-name .name .marker1{transform:rotate(-4deg);background:rgba(255,0,0,0.5)}.item-name .name .marker1::after{transform:rotate(4deg);background:rgba(255,0,0,0.5)}.item-name .name .marker2{transform:rotate(3deg);background:#ff9100}.item-name .name .marker2::after{transform:rotate(-5deg);background:rgba(255,145,0,0.8)}.item-name .name .marker3{transform:rotate(3deg);background:rgba(255,255,0,0.5)}.item-name .name .marker3::after{transform:rotate(-5deg);background:rgba(255,255,0,0.8)}.item-name .name .marker4{transform:rotate(1deg);background:#6adf26}.item-name .name .marker4::after{transform:rotate(1deg);background:rgba(106,223,38,0.7)}.item-name .name .marker5{transform:rotate(0deg);background:rgba(3,95,34,0.8)}.item-name .name .marker5::after{transform:translateX(10%) rotate(1deg);background:rgba(14,95,3,0.5)}.item-status{width:70px;text-align:center;border-radius:4px;cursor:pointer;padding:2px 0;font-family:'Indie Flower', cursive;line-height:1;font-weight:600;font-size:19px}.item-date{font-size:20px;font-family:'Indie Flower', cursive;line-height:1;font-weight:600}.item-date span{font-style:italic;font-weight:normal;font-size:10px}.item-delete svg{cursor:pointer;margin-left:15px}.item-delete svg path{transition:.3s}.item-delete svg:hover path{fill:#960000}@-webkit-keyframes marker{0%{width:0}100%{width:120%}}@keyframes marker{0%{width:0}100%{width:120%}}@-webkit-keyframes markerShadow{0%{width:0}100%{width:100%}}@keyframes markerShadow{0%{width:0}100%{width:100%}}

.list-help{background:wheat;padding:15px 40px;border-radius:10px;position:absolute;bottom:-125px;left:-50px;position:relative;height:120px;display:flex;justify-content:center;align-items:center;opacity:0.5;transition:.4s}.list-help:hover{opacity:1}.list-help:hover .tip::after,.list-help:hover .tip{opacity:1}.list-help::after{content:'Tips:';position:absolute;top:10px;left:10px;font-size:12px;font-style:italic}.list-help .tip{position:absolute;font-size:16px;font-weight:600;white-space:nowrap;border:1px solid rgba(0,0,0,0.2);padding:3px 5px;letter-spacing:0;opacity:0;transition:1s}.list-help .tip::after{content:'';position:absolute;width:20px;height:20px;border-top:1px solid #000;border-left:1px solid #000;opacity:0;transition:1s}.list-help .name{z-index:1}.list-help .name .tip{bottom:-35px;left:-20px}.list-help .name .tip::after{top:-14px;left:-7px;transform:rotate(-26deg)}.list-help .item-status{margin-left:20px;background:rgba(255,0,0,0.6);position:relative;cursor:inherit}.list-help .item-status .tip{top:-35px;left:50px}.list-help .item-status .tip::after{top:24px;left:17px;transform:rotate(-190deg)}.help-tip{position:fixed;font-size:16px;line-height:1.5;padding:5px 10px;border-radius:4px;text-align:left;font-family:'Gloria Hallelujah', cursive;color:#333;background:#d3b36e;box-shadow:3px 3px 3px rgba(0,0,0,0.3);pointer-events:none;z-index:100;transform:translateY(-100%)}.help-tip span{font-family:sans-serif}

.add-item{margin-left:50px;margin-top:25px}.add-item button{padding:4px 15px;border:none;border-radius:4px;background:#960000;font-size:20px;font-family:'Indie Flower', cursive;color:khaki;cursor:pointer;outline:none}.add-item button:active{background:#690000}.add-popup.active,.add-popup-body.active{visibility:visible;opacity:1}.add-popup{visibility:hidden;opacity:0;transition:.2s;position:absolute;top:0;left:0;width:100vw;height:100vh;background:rgba(0,0,0,0.3);z-index:10}.add-popup-body{visibility:hidden;opacity:0;transition:.2s;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);background:wheat;border-radius:10px;padding:30px 20px;text-align:center;z-index:11}.add-popup-body input[type='text']{border:none;background:rgba(255,255,255,0.658);margin-bottom:10px;font-size:20px;padding:4px 20px;border-radius:4px;outline:none;width:100%}.add-popup-body form{margin-top:10px}.add-popup-body form label{opacity:0.3;padding:4px 6px;cursor:pointer;position:relative}.add-popup-body form label::after{position:absolute;content:'';width:110%;top:-1px;left:-5px;height:100%;z-index:-1}.add-popup-body form button{margin-top:20px;border:none;outline:none;background:none;cursor:pointer;font-size:24px;font-weight:600;letter-spacing:3px}.add-popup-body input[type='radio']{width:0.01px}.add-popup-body input[type='radio']:checked+label{opacity:1}.add-popup-body input[type='radio']:nth-of-type(1)+label{transform:rotate(-4deg);box-shadow:inset 0 -30px 0 rgba(255,0,0,0.5)}.add-popup-body input[type='radio']:nth-of-type(1)+label::after{transform:rotate(4deg);background:rgba(255,0,0,0.5)}.add-popup-body input[type='radio']:nth-of-type(2)+label{transform:rotate(3deg);box-shadow:inset 0 -30px 0 #ff9100}.add-popup-body input[type='radio']:nth-of-type(2)+label::after{transform:rotate(-5deg);background:rgba(255,145,0,0.8)}.add-popup-body input[type='radio']:nth-of-type(3)+label{transform:rotate(3deg);box-shadow:inset 0 -30px 0 rgba(255,255,0,0.5)}.add-popup-body input[type='radio']:nth-of-type(3)+label::after{transform:rotate(-5deg);background:rgba(255,255,0,0.5)}.add-popup-body input[type='radio']:nth-of-type(4)+label{box-shadow:inset 0 -30px 0 #6adf26}.add-popup-body input[type='radio']:nth-of-type(4)+label::after{transform:rotate(1deg);background:rgba(106,223,38,0.7)}.add-popup-body input[type='radio']:nth-of-type(5)+label{transform:rotate(0deg);box-shadow:inset 0 -30px 0 rgba(3,95,34,0.8)}.add-popup-body input[type='radio']:nth-of-type(5)+label::after{transform:translateX(10%) rotate(1deg);background:rgba(14,95,3,0.5)}.add-popup-body .invalid{position:relative}.add-popup-body .invalid::after{position:absolute;content:'Fill it!';color:red;font-size:10px;top:-15px;right:0}

